import * as React from 'react'
import { Link, graphql } from 'gatsby'

import Layout from '../components/Layout'
import Seo from '../components/Seo'
import { rhythm } from '../utils/typography'

const TagTemplate = ({ data, location, pageContext }) => {
  const { tag } = pageContext
  const siteTitle = data.site.siteMetadata?.title || 'Title'
  const posts = data.allMarkdownRemark.nodes
  const totalCount = data.allMarkdownRemark.totalCount

  return (
    <Layout location={location} title={siteTitle}>
      <h1
        style={{
          fontFamily: "'Playfair Display', Georgia, serif",
          textAlign: 'center',
          borderBottom: '2px solid #1a1a1a',
          paddingBottom: rhythm(0.5),
        }}
      >
        Dispatches on "{tag}"
      </h1>
      <p
        style={{
          textAlign: 'center',
          fontFamily: "'Libre Baskerville', Georgia, serif",
          fontStyle: 'italic',
          color: '#666',
          marginBottom: rhythm(1.5),
        }}
      >
        {totalCount} article{totalCount === 1 ? '' : 's'} catalogued.{' '}
        <Link to="/tags/" style={{ color: '#8b4513' }}>
          View all subjects
        </Link>
      </p>
      {posts.map((post, index) => {
        const title = post.frontmatter.title || post.fields.slug

        return (
          <article
            key={post.fields.slug}
            style={{
              marginBottom: rhythm(1.5),
              paddingBottom: rhythm(1.5),
              borderBottom: index < posts.length - 1 ? '1px solid rgba(139, 69, 19, 0.2)' : 'none',
            }}
          >
            <header>
              <h3
                style={{
                  fontFamily: "'Playfair Display', Georgia, serif",
                  marginBottom: rhythm(1 / 4),
                  marginTop: 0,
                }}
              >
                <Link
                  style={{
                    boxShadow: 'none',
                    color: '#1a1a1a',
                    borderBottom: 'none',
                  }}
                  to={post.fields.slug}
                >
                  {title}
                </Link>
              </h3>
              <small
                style={{
                  fontFamily: "'Libre Baskerville', Georgia, serif",
                  fontStyle: 'italic',
                  color: '#666',
                }}
              >
                {post.frontmatter.date}
              </small>
            </header>
            <section>
              <p
                dangerouslySetInnerHTML={{
                  __html: post.frontmatter.description || post.excerpt,
                }}
                style={{
                  marginTop: rhythm(0.5),
                  marginBottom: 0,
                  color: '#4a4a4a',
                }}
              />
            </section>
          </article>
        )
      })}
    </Layout>
  )
}

export const Head = ({ pageContext, data }) => {
  const siteTitle = data.site.siteMetadata?.title
  return <Seo title={`Posts tagged "${pageContext.tag}" | ${siteTitle}`} />
}

export default TagTemplate

export const pageQuery = graphql`
  query TagPage($tag: String!) {
    site {
      siteMetadata {
        title
      }
    }
    allMarkdownRemark(
      filter: { frontmatter: { tags: { in: [$tag] } } }
      sort: { frontmatter: { date: DESC } }
    ) {
      totalCount
      nodes {
        excerpt
        fields {
          slug
        }
        frontmatter {
          date(formatString: "DD MMMM, YYYY")
          title
          description
        }
      }
    }
  }
`
